export default {"name":"泰国茶","img":"\\abroads\\thailand\\tgc.jpg","desc":["在世界某些地方饮用泰国茶是一种生活方式，泰国茶还以一些令人惊讶的方式提升您的整体健康和健康。"],"menus":[{"name":"什么是泰国茶","content":[{"text":"泰国茶是泰国一种流行的传统饮料，无论是热饮还是冷饮，通常都是用茶，糖和牛奶制成的。虽然这些成分听起来不是特别有趣或独特，但茶混合物往往是当地的锡兰品种，并且还有许多其他成分，香料或改变，取决于谁正在准备茶。除了其他流行的变化之外，还可以添加不同类型的牛奶，八角茴香或罗望子种子。这种奶油和泡沫饮料可以是浅色或深色的，更常用的是冰。话虽如此，在早晨，热泰国茶也是一个受欢迎的选择。"},{"text":"就咖啡因而言，每8盎司杯饮料中含有50至60毫克，约为平均一杯咖啡的一半。也许泰国茶的独特之处在于其奇特的颜色-颜色往往呈橙色，琥珀色或淡红色。由于使用的叶子是基本的红茶叶，有些人对他们的饮料的最终外观感到困惑。事实证明，在泰国，这种颜色可能来自于包含罗望子种子或其他色彩缤纷的香料，以及茶叶为水染色的自然方式。可以在美国或欧洲购买的泰国茶，红色或黄色食物通常添加着色，使泰国茶具有独特的外观。"}]},{"name":"泰国茶的变化","content":[{"text":"泰国茶的基本变化包括热泰国茶和冷泰国茶。"},{"text":"热泰国茶"},{"text":"泰国茶的热门品种包括泰国热茶，它是热的，然后加入牛奶和糖。另一种品种，深色泰式热茶，可以消除牛奶，因此饮料只加糖加糖。这两个品种都可能包括其他香料和调味料，具体取决于您的配方或偏好。"},{"text":"冷泰国茶"},{"text":"当供应这种饮料冷饮时，一种变化可能是深色泰式冰茶，这只是含糖的基本酿造，而不是为了泡沫外观添加牛奶。另一种变体称为酸橙茶，其中深色泰式冰茶用石灰，糖和薄荷加糖。"}]},{"name":"泰国茶的营养成分","content":[{"text":"这种特色茶是糖，蛋白质和脂肪的良好来源，取决于您如何准备饮料。适度也是关键，因为有很多卡路里，每杯180-300卡路里。然而，这一切都来自糖和牛奶；茶本身是一种不含卡路里的饮料。同样，根据您的制备方法，您可能还会获得显着水平的维生素A，维生素D和钙。茶叶中还含有显着的抗氧化剂和活性成分，可以促进整体健康。"}]},{"name":"泰国茶的好处","content":[{"text":"泰国茶最显着的好处包括其对胆固醇水平，体重减轻，压力和癌症预防等方面的影响。"},{"text":"癌症"},{"text":"与许多红茶一样，这种类型的泰国茶具有许多强大的抗氧化剂，可以帮助减少氧化应激，防止体内自由基的负面影响。这将防止可导致癌症的细胞突变。"},{"text":"减肥"},{"text":"尽管这种饮料的高热量计，泰国茶可以说是非常充盈，咖啡因可以在某些情况下抑制饥饿感，防止暴饮暴食，帮助你与你的减肥目标。"},{"text":"能源"},{"text":"咖啡因是提高一个很好的方法既新陈代谢和你的整体能量水平，帮助你燃烧更多的脂肪和更加活跃。"},{"text":"心脏健康"},{"text":"研究表明，红茶品种可以帮助保护血管和动脉的完整性，从而降低动脉粥样硬化，心脏病发作和中风的风险。"},{"text":"哮喘"},{"text":"对泰国茶的研究发现，它可以缓解喉咙炎症，防止刺激和过敏反应，以及哮喘哮喘的家庭疗法哮喘的家庭疗法包括深呼吸，印度醋栗，生姜，鼓槌……症状。"}]}]}